import React from "react";
import * as section from "./../components/homepage/";
import { withState } from "./../Context";

const HomePage = withState(
  ({
    state: {
      homePage: {
        visibileSections: {
          bookingFormSection,
          rabSection,
          loparSection,
          servicesSection,
          apartmentsSection,
          testimonialsSection
        }
      }
    }
  }) => (
    <>
      {bookingFormSection && <section.BookingFormSection />}
      {(rabSection || loparSection) && <section.RabLoparSection />}
      {servicesSection && <section.ServicesSection />}
      {apartmentsSection && <section.ApartmentsSection />}
      {testimonialsSection && <section.TestimonialsSection />}
    </>
  )
);

export default HomePage;
