import React, { useState } from "react";
import { Container, Row, Col, Form, Button, Modal } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "./booking-form.scss";
import { withState } from "./../../Context";

const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};

const BookingFormSection = withState(
  ({
    state: {
      translations: { t }
    }
  }) => {
    const [modalShow, setModalShow] = useState(false);
    const [form, setForm] = useState({
      email: "",
      arrival: "",
      departure: ""
    });

    const handleSubmit = (e) => {
      fetch("/", {
        method: "POST",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: encode({ "form-name": "booking-info", ...form })
      })
        .then(() => setModalShow(true))
        .catch((error) => alert(error));

      e.preventDefault();
    };

    const iOS = () => {
      return (
        [
          "iPad Simulator",
          "iPhone Simulator",
          "iPod Simulator",
          "iPad",
          "iPhone",
          "iPod"
        ].includes(navigator.platform) ||
        // iPad on iOS 13 detection
        (navigator.userAgent.includes("Mac") && "ontouchend" in document)
      );
    };

    const handleChange = (e) => {
      setForm((state) => ({ ...state, [e.target.name]: e.target.value }));
    };
    const { email, arrival, departure } = form;

    return (
      <div id="banner" style={{ height: "500px" }}>
        <Container>
          <Form
            onSubmit={handleSubmit}
            className="px-2 py-3 px-md-5 py-md-4 banner-search"
          >
            <Row>
              <Form.Group
                as={Col}
                sm={12}
                md={4}
                className="m-0 px-4 px-md-2 py-2 py-md-0"
              >
                <Form.Label className="ml-2">
                  {t("bookingFormEmailLabel")}
                </Form.Label>
                <Form.Control
                  type="email"
                  placeholder={t("bookingFormEmailPlaceholder")}
                  required
                  name="email"
                  value={email}
                  onChange={(e) => handleChange(e)}
                />
              </Form.Group>
              <Form.Group
                as={Col}
                sm={12}
                md={3}
                className="m-0 px-4 px-md-2 py-2 py-md-0"
              >
                <Form.Label className="ml-2">
                  {t("bookingFormArrivalLabel")}
                </Form.Label>

                {
                  // show native datepicker on iOS
                }
                {!iOS() ? (
                  <Form.Control
                    as={DatePicker}
                    placeholderText={t("bookingFormArrivalPlaceholder")}
                    selected={arrival && new Date(arrival)}
                    required
                    showPopperArrow={false}
                    minDate={new Date()}
                    onChange={(date) => {
                      handleChange({
                        target: {
                          name: "arrival",
                          value: date.toDateString()
                        }
                      });
                    }}
                    selectsStart
                    startDate={arrival && new Date(arrival)}
                    endDate={departure && new Date(departure)}
                  />
                ) : (
                  <Form.Control
                    type="date"
                    className="datepicker-wrapper"
                    placeholder={t("bookingFormArrivalPlaceholder")}
                    onChange={(date) => {
                      handleChange({
                        target: {
                          name: "arrival",
                          value: date.target.value
                        }
                      });
                    }}
                  />
                )}
              </Form.Group>
              <Form.Group
                as={Col}
                sm={12}
                md={3}
                className="m-0 px-4 px-md-2 py-2 py-md-0"
              >
                <Form.Label className="ml-2">
                  {t("bookingFormDepartureLabel")}
                </Form.Label>

                {
                  // show native datepicker on iOS
                }
                {!iOS() ? (
                  <Form.Control
                    as={DatePicker}
                    placeholderText={t("bookingFormDparturePlaceholder")}
                    selected={departure && new Date(departure)}
                    required
                    showPopperArrow={false}
                    onChange={(date) => {
                      handleChange({
                        target: {
                          name: "departure",
                          value: date.toDateString()
                        }
                      });
                    }}
                    selectsEnd
                    startDate={arrival && new Date(arrival)}
                    endDate={departure && new Date(departure)}
                    minDate={arrival && new Date(arrival)}
                  />
                ) : (
                  <Form.Control
                    type="date"
                    className="datepicker-wrapper"
                    placeholder={t("bookingFormDparturePlaceholder")}
                    onChange={(date) => {
                      handleChange({
                        target: {
                          name: "departure",
                          value: date.target.value
                        }
                      });
                    }}
                  />
                )}
              </Form.Group>
              <Form.Group
                as={Col}
                sm={12}
                md={2}
                className="m-0 px-4 px-md-2 py-3 py-md-0 text-center align-self-end"
              >
                <Button type="submit">
                  <i className="fa fa-send mr-2"></i>
                  {t("bookingFormInquiryLabel")}
                </Button>
              </Form.Group>
            </Row>
          </Form>
        </Container>
        <Modal
          show={modalShow}
          onHide={() => {
            setModalShow(false);
            setForm({
              email: "",
              arrival: "",
              departure: ""
            });
          }}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <Modal.Title id="contained-modal-title-vcenter">
              {t("bookingFormInquiryModalTitle")}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>
              {t("bookingFormInquiryModalSuccessMsg")}
              <br />
              <br />
              <strong>{t("bookingFormArrivalLabel")}:</strong>{" "}
              {arrival && arrival}
              <br />
              <strong>{t("bookingFormDepartureLabel")}:</strong>{" "}
              {departure && departure}
              <br />
              <br />
              <div
                dangerouslySetInnerHTML={{
                  __html: t("bookingFormInquiryModalWillGetBackMsg", {
                    email: email
                  })
                }}
              ></div>
            </p>
          </Modal.Body>
          <Modal.Footer>
            <Button
              onClick={() => {
                setModalShow(false);
                setForm({
                  email: "",
                  arrival: "",
                  departure: ""
                });
              }}
            >
              {t("closeBtnLabel")}
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
);

export default BookingFormSection;
