import React from "react";
import { HashLink as Link } from "react-router-hash-link";
import "./services.scss";
import { Container, Row, Col } from "react-bootstrap";
import * as serviceIcon from "./../../img/service-icons";
import { withState } from "./../../Context";

const ServicesSection = withState(
  ({
    state: {
      translations: { t },
      scrollWithOffset
    }
  }) => {
    const servicesData = [
      {
        icon: "Beaches",
        title: t("islandRabPageBeachesSectionTitle"),
        content: t("islandRabPageBeachesSectionContent")
      },
      {
        icon: "Fun",
        title: t("islandRabPageFunSectionTitle"),
        content: t("islandRabPageFunSectionContent")
      },
      {
        icon: "ActiveHolidays",
        title: t("islandRabPageActiveHolidaysSectionTitle"),
        content: t("islandRabPageActiveHolidaysSectionContent")
      },
      {
        icon: "GastronomicOffer",
        title: t("islandRabPageGastronomicOfferSectionTitle"),
        content: t("islandRabPageGastronomicOfferSectionContent")
      },
      {
        icon: "History",
        title: t("islandRabPageHistorySectionTitle"),
        content: t("islandRabPageHistorySectionContent")
      },
      {
        icon: "Arrival",
        title: t("islandRabPageArrivalSectionTitle"),
        content: t("islandRabPageArrivalSectionContent")
      }
    ];

    return (
      <section className="content-section services" id="island">
        <Container>
          <Row>
            {servicesData.map((service, index) => {
              const { icon, title, content } = service;

              return (
                content !== "" && (
                  <Col sm={6} md={4} className="mt-4" key={index}>
                    <div className="wrapper">
                      <p
                        className="service-icon"
                        style={{
                          background: `url(${serviceIcon[icon]}) #01CD9E center no-repeat`
                        }}
                      ></p>
                      <h3 className="service-title">{title}</h3>
                      <p
                        className="info"
                        dangerouslySetInnerHTML={{ __html: content }}
                      ></p>
                      <Link
                        className="btn btn-primary"
                        smooth
                        scroll={(el) => scrollWithOffset(el)}
                        to={`/rab-island#${icon}`}
                      >
                        {t("readMoreLabel")}
                      </Link>
                    </div>
                  </Col>
                )
              );
            })}
          </Row>
        </Container>
      </section>
    );
  }
);

export default ServicesSection;
